:root {
  --main-red: rgb(211,9,19);
  --main-red-hover: #e50914;
  --main-dark: rgb(20,20,20);
  --main-deep-dark: rgb(0,0,0);
  --main-gray: rgb(153,153,153);
}

* {
  box-sizing: border-box;
}

body {
  font-family: 'Helvetica', sans-serif;
  -webkit-font-smoothing: antialiased;
  background: var(--main-dark);
  color: #FFFFFF;
  overflow-x: hidden;
}

a{
  text-decoration: none;
  //color: #FFFFFF;
}

/* Header section */
.header-container {
  width: 100vw;
  height: 90vh;
  position: relative;
  background: radial-gradient(rgba(0,0,0,0), rgba(0,0,0,0.7)), url("../images/bg.jpg") no-repeat center center/cover;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    box-shadow: inset 4.375rem 3.125rem 11.875rem #000000, inset -4.375rem -3.125rem -11.875rem #000000;
  }
}